@charset "utf-8";

body{
  height: 100vh; background: url(../images/bg.jpg) no-repeat center/cover fixed;
}

.container{
  padding: 50px 0;

  .heroes{
    display: flex; flex-wrap: wrap; justify-content: center;
    max-width: 660px; margin: 0 auto; padding: 40px 20px;

    .hero{
      width: 80px; height: 84px; margin: 4px; border: 3px solid #fff; box-sizing: border-box; overflow: hidden; background-color: #555; transform: skewX(-14deg); transition: transform .1s, background-color .6s;

      &:hover{
        background-color: #ff9300;
        transform: skewX(-14deg) scale(1.3);
        z-index: 1;
        box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.628);
      }

      .image{
        width: 140%; height: 100%;
        transform: translateX(-16px) skewX(14deg);
        background-position: center;
        background-size: 90px;
        background-repeat: no-repeat;
      }

      @for $i from 1 through 32{
        &:nth-child(#{$i}) .image{  background-image: url(../images/hero#{$i}.png);}
      }

    }
  }
  .logo{
    max-width: 300px; margin: 0 auto; padding: 0 20px;
  
    img{
      width: 100%;
    }
    
  }
  
}





































